import ItemService from './ItemService';
export default {
  name: 'SwitchWidget',
  props: {
    config: {
      type: Object,
      required: true
    }
  },
  computed: {
    label: function label() {
      return ItemService.getLabel(this.config.label);
    },
    value: function value() {
      return ItemService.getValue(this.config.label, this.config.item.state);
    }
  }
};